<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <div class="input-group w-25" style="margin-right: auto;margin-left: auto;">
          <div class="input-group-prepend">
            <label class="input-group-text" for="inputGroupSelect01">Difficulty</label>
          </div>
          <select class="custom-select" id="inputGroupSelect01">
            <option selected value="1">Unbeatable</option>
            <option value="2">Hard</option>
            <option value="3">Moderate</option>
            <option value="3">Easy</option>
            <option value="3">Cant Lose</option>
          </select>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <b-form-group label="Players Turn">
          <b-form-radio-group
            id="btn-radios-1"
            v-model="selected"
            :options="options"
            buttons
            name="radios-btn-default"
          ></b-form-radio-group>
        </b-form-group>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <button
          type="button"
          class="btn btn-outline-light w-25"
          @click="startGame"
        >Start Game</button>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <button
          type="button"
          class="btn btn-outline-light w-25"
          @click="setMenu(CONSTANTS.PLAY_MENU)"
        >Back</button>
      </div>
    </div>
  </div>
</template>

<script>
import * as CONSTANTS from "@/store/constants";

export default {
  name: "SinglePlayerMenu",
  components: {},
  data() {
    return {
      CONSTANTS: CONSTANTS,
      selected: 'X',
        options: [
          { text: 'First', value: 'X' },
          { text: 'Second', value: '0' }
        ]
    };
  },
  methods: {
    setMenu(menu) {
      this.$store.dispatch(CONSTANTS.SET_VIEW, menu);
    },
    startGame() {
      this.$store.dispatch(CONSTANTS.NEW_GAME, {type:CONSTANTS.SINGLE_PLAYER});
      this.setMenu(CONSTANTS.TIC_TAC_TOE_GAME);
    }
  }
};
</script>

