import * as CONSTANTS from './constants';

export default {
    [CONSTANTS.SET_VIEW]:({commit}, payload) => {
        commit(CONSTANTS.SET_VIEW,payload);
    },
    [CONSTANTS.NEW_GAME]:({commit}, payload) => {
       commit(CONSTANTS.NEW_GAME,payload);
    },
    [CONSTANTS.UPDATE_GAME_SQUARE]:({commit}, payload) => {
       commit(CONSTANTS.UPDATE_GAME_SQUARE,payload);
    }
}