<template>
  <div class="container">
    <div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="setMenu(CONSTANTS.SINGLE_PLAYER_MENU)">1 Player</button>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="setMenu(CONSTANTS.TWO_PLAYER_MENU)">2 Player</button>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="setMenu(CONSTANTS.MAIN_MENU)">Back</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as CONSTANTS from "@/store/constants";

export default {
  name: "app",
  components: {},
  data() {
    return {
      CONSTANTS:CONSTANTS
    };
  },
  methods: {
    setMenu(menu) {
      this.$store.dispatch(CONSTANTS.SET_VIEW, menu);
    }
  }
};
</script>

