<template>
  <div class="container">
    <div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="startGame">Start Game</button>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="setMenu(CONSTANTS.PLAY_MENU)">Back</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as CONSTANTS from "@/store/constants";

export default {
  name: "TwoPlayerMenu",
  components: {},
  data() {
    return {
      CONSTANTS: CONSTANTS
    };
  },
  methods: {
    setMenu(menu) {
      this.$store.dispatch(CONSTANTS.SET_VIEW, menu);
    },
    startGame() {
      this.$store.dispatch(CONSTANTS.NEW_GAME, {type:CONSTANTS.TWO_PLAYER});
      this.setMenu(CONSTANTS.TIC_TAC_TOE_GAME);
    }
  }
};
</script>

