<template>
  <div class="container" v-if="game !== null">
    <div class="row">
      <div class="col">
        <div class="w-25" style="margin-right:auto;margin-left:auto;">
          <Grid ref="grid" :game="game"></Grid>
          Winner: {{winner}}
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col">
        <button
          type="button"
          class="btn btn-outline-light w-25"
          @click="setMenu(CONSTANTS.GAME_SUMMARY_MENU)"
        >Game Summary</button>
      </div>
    </div>
    <HelpModal ref="helpModal"></HelpModal>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import * as CONSTANTS from "@/store/constants";
import Grid from "@/components/grid.vue";
import HelpModal from "@/components/menus/help-menu.vue";

export default {
  name: "TicTacToeGame",
  components: {
    Grid,
    HelpModal
  },
  computed: {
    ...mapGetters({
      game: CONSTANTS.GET_GAME
    }),
    winner(){
      if(this.game.winner !== ''){
        this.endGame();
      }
      return this.game.winner;
    }
  },
  data() {
    return {
      CONSTANTS: CONSTANTS
    };
  },
  methods: {
    showHelpModal() {
      this.$refs.helpModal.show();
    },
    setMenu(menu) {
      this.$store.dispatch(CONSTANTS.SET_VIEW, menu);
    },
    endGame(){
      this.$refs.grid.endGame();
    }
  }
};
</script>

