<template>
  <div class="container" v-if="game">
    <div>
      <div class="row">
        <div class="col">
          Game Summary
        </div>
      </div>
      <div class="row">
        <div class="col">
          Winner: {{game.winner}}
        </div>
      </div>
      <div class="row">
        <div class="col">
          Playing Time: {{hours + ':' + minutes + ':' + seconds}}
        </div>
      </div>
      <div class="row">
        <div class="col">
           <button type="button" class="btn btn-outline-light w-25">Replay</button>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="setMenu(CONSTANTS.PLAY_MENU)">Play Again</button>
        </div>
      </div>
      <div class="row">
        <div class="col">
          <button type="button" class="btn btn-outline-light w-25" @click="setMenu(CONSTANTS.MAIN_MENU)">Main Menu</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import {differenceInSeconds,differenceInMinutes,differenceInHours} from 'date-fns'
import * as CONSTANTS from "@/store/constants";

export default {
  name: "app",
  components: {},
    computed: {
    ...mapGetters({
      game: CONSTANTS.GET_GAME
    }),
    seconds(){
      let seconds = differenceInSeconds(this.game.endTime,this.game.startTime) % 60;
      return seconds > 10 ? seconds : "0" + seconds;
    },
    minutes(){
      let minutes  = differenceInMinutes(this.game.endTime,this.game.startTime) % 60;
      return minutes > 10 ? minutes : "0" + minutes;
    },
    hours(){
      let hours = differenceInHours(this.game.endTime,this.game.startTime) % 24; 
      return hours > 10 ? hours : "0" + hours;
    }
  },
  data() {
    return {
      CONSTANTS:CONSTANTS
    };
  },
  methods: {
    setMenu(menu) {
      this.$store.dispatch(CONSTANTS.SET_VIEW, menu);
    }
  }
};
</script>

