import * as CONSTANTS from './constants';
import UUID from 'pure-uuid';
import Vue from 'vue';

const winningCombinations = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6]
];

const letters = ["X", "O"];


function determineWinner(game) {
    let result = {
        winner: false
    };

    winningCombinations.forEach(combination => {
        let A = game.board[combination[0]].letter;
        let B = game.board[combination[1]].letter;
        let C = game.board[combination[2]].letter;

        if (A === B && A === C && A !== " ") {
            result.winner = true;
            result.combination = combination;
            result.letter = A;
        }
    })

    return result;
}


export default {
    [CONSTANTS.SET_VIEW]: (state, payload) => {
        state.view = payload;
    },
    [CONSTANTS.NEW_GAME]: (state, payload) => {
        const board = [];
        for (var i = 0; i < 9; i++) {
            board.push({letter:" ",locked:false});
        }

        const gameObj = {
            id: new UUID(1).format('std'),
            type: payload.type,
            board,
            isActive: true,
            moves: [],
            winner: '',
            startTime: new Date(),
            endTime:null
        }
        state.activeGame = gameObj;
    },
    [CONSTANTS.UPDATE_GAME_SQUARE]: (state, payload) => {
        let letter = letters[state.activeGame.moves.length % 2];
        Vue.set(state.activeGame.board[payload], 'letter', letter);
        Vue.set(state.activeGame.board[payload], 'locked', true);
        Vue.set(state.activeGame.moves, state.activeGame.moves.length, { letter, index: payload.letter });

        if (state.activeGame.moves.length >= 5) {
            let result = determineWinner(state.activeGame);

            if (result.winner) {
                console.log('We have a winner!');
                console.log("Combination", result.combination);
                console.log("Letter", result.letter);
                const letter = result.letter;
                Vue.set(state.activeGame, 'isActive', false);
                Vue.set(state.activeGame, 'winner', letter);
                Vue.set(state.activeGame, 'endTime', new Date());

                for(var i=0; i<9; i++){
                    Vue.set(state.activeGame.board[i], 'locked', true);
                }
            }
        }
    },
}